import React, { useState, Fragment, useContext } from "react";
import Button from "../../shared/Components/FormComponents/Button";
import Card from "../../shared/Components/UIElement/Card";
import Modal from "../../shared/Components/UIElement/Modal";
import Map from "../../shared/Components/UIElement/Map";
import "./Placeitem.css";
import AuthContext from "../../shared/Components/context/Auth-context";
import { useHttpClient } from "../../shared/Hooks/http-hook";
import ErrorModal from "../../shared/Components/UIElement/ErrorModal";
import LoadingSpinner from "../../shared/Components/UIElement/LoadingSpinner";
const Placeitem = (props) => {
  const auth = useContext(AuthContext);
  const [showMap, setshowonMap] = useState(false);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const showmapHandler = () => {
    setshowonMap(true);
  };
  const closemapHandler = () => setshowonMap(false);
  const showDeleteWarningHandler = () => {
    setShowConfirmModal(true);
  };
  const cancelDeleteHandler = () => {
    setShowConfirmModal(false);
  };
  const confirmdeleteHandler = async () => {
    // event.preventDefault();
    setShowConfirmModal(false);
    try {
      await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/places/${props.id}`,
        "DELETE",
        null,
        {
          Authorization: "Bearer " + auth.token,
        }
      );
      props.onDelete(props.id);
    } catch (err) {}
  };
  return (
    <Fragment>
      {error && <ErrorModal error={error} onClear={clearError} />}
      {showMap && (
        <Modal
          show={showMap}
          onCancel={closemapHandler}
          header={props.address}
          contentClass="place-item__modal-content"
          footerClass="place-items__modal-actions"
          footer={<Button onClick={closemapHandler}>CLOSE</Button>}
        >
          <div className="map-container">
            <Map center={props.coordinates} zoom={16} />
          </div>
        </Modal>
      )}
      {showConfirmModal && (
        <Modal
          show={showConfirmModal}
          onCancel={cancelDeleteHandler}
          header="Are you sure?"
          footerClass="place-item__modal-actions"
          footer={
            <Fragment>
              <Button inverse onClick={cancelDeleteHandler}>
                CANCEL
              </Button>
              <Button danger onClick={confirmdeleteHandler}>
                DELETE
              </Button>
            </Fragment>
          }
        >
          <p>
            Do you want to proceed and delete this place?Please note that is
            can't be undone thereafter.
          </p>
        </Modal>
      )}
      <li className="place-item">
        <Card className="place-item__content">
          {isLoading && <LoadingSpinner asOverlay />}
          <div className="place-item__image">
            <img
              src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`}
              alt={props.title}
            />
          </div>
          <div className="place-item__info">
            <h2>{props.title}</h2>
            <h3>{props.address}</h3>
            <p>{props.description}</p>
          </div>
          <div className="place-item__actions">
            <Button onClick={showmapHandler} inverse>
              VIEW ON MAP
            </Button>
            {auth.userId === props.creatorid && (
              <Button to={`/places/${props.id}`}>EDIT</Button>
            )}
            {auth.userId === props.creatorid && (
              <Button danger onClick={showDeleteWarningHandler}>
                DELETE
              </Button>
            )}
          </div>
        </Card>
      </li>
    </Fragment>
  );
};

export default Placeitem;
